import React from 'react';
import { Route, Redirect, Switch, useLocation } from 'react-router-dom';
import Home, { useWindowSize } from '../Home';
import Therapies from '../Therapies';
import Contact from '../Contact';
import About from '../About';
import Nav from './Nav';
import Footer from './Footer';
import { StyledBox } from './styles';
import Background from '../../static/Home/Background.jpg';

const App = () => {
    const location = useLocation();
    // const [, windowHeight] = useWindowSize();

    return (
        <StyledBox
            background={location.pathname === '/' ? `url(${Background})` : 'none'}
            overflowy={location.pathname === '/' ? 'hidden' : 'inherit'}
            maxwidth={location.pathname === '/' ? 'inherit' : '48rem'}
        >
            {/* <StyledBox height={windowHeight} overflowY={location.pathname === '/' ? 'hidden' : 'inherit'}> */}
            <Nav />

            <Switch className="content" location={location}>
                <Route path="/therapies"><Therapies /></Route>
                <Route exact path="/contact"><Contact /></Route>
                <Route exact path="/about"><About /></Route>
                <Route exact path="/"><Home /></Route>
                <Route component={() => <Redirect to="/" />} />
            </Switch>

            {location.pathname !== '/' && <Footer />}
        </StyledBox>
    );
};

export default App;
